
.header-animado{
    $h:300px;
    background-color: $main-color;
    padding-top: 96px; 
    margin-bottom: 0;
    padding-bottom: 0;


   .header-animado__title{
       color: #00151a;
       font-size: 38px; 
       font-weight: bold;
   }
   .header-animado__subtitle{
    color: white;
    font-size: 34px;
    font-weight: 300;
    line-height: 1.5; 
   }

   &__ilustra{
       background-image: url(../imagens/cenario_all_960.png);
       margin-top: 8%; 
       height: $h/2;
       background-size: auto 100%;
       background-position: center top;

       &>div{
            display: none;
       }  

       @media screen and (min-width: 480px){
           background-image: url(../imagens/cenario_all_1530.png);
        }
        @media screen  and (min-width:960px){
            background-image: url(../imagens/cenario_bg.png); 
            height: $h;
            position: relative;

            &>div{
                display: block;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
            }

            .nuvens{ 
                background-image: url(../imagens/cenario_nuvens.png);
            }
        }
   }
}

